/* variables */
:root {
  --base-color: #E47512;
  --secondary-color: #F0A50F;
  --dark-gray: #292d36;
  --ternary-color: #06923F;
  --accent-color: #D2EBCC;
  --medium-gray: #808291;
  --primary-font: "Kumbh Sans", sans-serif;
  --alt-font: "cerebri-sans-regular";
}

.reset-for-wysiwyg ul {
  margin: 0;
  padding: 15px;
}
.reset-for-wysiwyg ul li {
  list-style-type: initial;
}

@keyframes opacityMenu {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
/* header */
/* header */
@media (max-width: 767.98px) {
  .sticky .navbar, .sticky-active .navbar {
    top: 0px !important;
  }
}

.navbar .navbar-nav .nav-link {
  font-size: 18px;
  font-family: "cerebri-sans-medium" !important;
}

.navbar .navbar-nav .nav-item.active .nav-link {
  opacity: 1;
  color: var(--base-color);
}

.navbar .navbar-nav .dropdown .dropdown-menu a {
  font-size: 16px;
  line-height: 28px;
  font-family: "cerebri-sans-regular" !important;
}

header .btn.btn-small {
  font-size: 14px;
}

header .btn.btn-small i {
  top: -2px;
}

header .navbar-brand img {
  max-height: 50px;
}

footer .footer-logo img {
  max-height: 50px;
}

.navbar .navbar-nav .dropdown.dropdown-with-icon-style02 .dropdown-menu {
  width: 300px;
}

.navbar .navbar-nav .dropdown.dropdown-with-icon-style02 .dropdown-menu li a {
  border-bottom: 1px solid var(--very-light-gray);
  padding: 20px 30px;
  font-size: 17px;
  color: var(--dark-gray) !important;
  font-family: "cerebri-sans-medium" !important;
}

.navbar .navbar-nav .dropdown.dropdown-with-icon-style02 .dropdown-menu li a:hover {
  background-color: var(--very-light-gray);
}

.navbar .navbar-nav .dropdown.dropdown-with-icon-style02 .dropdown-menu li a img {
  width: 40px;
  margin-right: 10px;
}

.sec-banner {
  margin-top: 50px;
}
@media (min-width: 992px) {
  .sec-banner {
    margin-top: 0;
  }
}

footer .useful-links ul > li a.nav-link {
  padding: 0;
}

.wp-block-file > a {
  font-family: var(--alt-font);
  font-size: 1rem;
  text-decoration: underline;
}

.has-drop-cap:first-letter {
  font-weight: 700;
  font-size: 3rem !important;
  padding-right: 0.08em;
}

.general-page-header.dark-overlay::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
}

.text-ternary-color {
  color: var(--ternary-color);
}

.bg-ternary-color {
  background-color: var(--ternary-color);
}

.text-secondary-color {
  color: var(--secondary-color);
}

.bg-secondary-color {
  background-color: var(--secondary-color);
}

.text-w-image__image-wrapper {
  width: 100%;
  margin-right: 40px;
}
@media (min-width: 768px) {
  .text-w-image__image-wrapper {
    width: 50%;
    float: left;
  }
}

.row-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
}
@media (min-width: 1400px) {
  .row-grid {
    grid-template-columns: repeat(4, minmax(300px, 1fr));
  }
}
@media (min-width: 992px) {
  .row-grid {
    grid-template-columns: repeat(3, minmax(300px, 1fr));
  }
}
.row-grid--4 {
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}
@media (min-width: 992px) {
  .row-grid--4 {
    grid-template-columns: repeat(4, minmax(250px, 1fr));
  }
}
.row-grid.gap-1 {
  gap: 10px;
}
.row-grid.gap-2 {
  gap: 20px;
}
.row-grid.gap-3 {
  gap: 30px;
}
.row-grid.gap-4 {
  gap: 40px;
}
.row-grid.gap-5 {
  gap: 50px;
}
.row-grid.gap-half-1 {
  gap: 5px;
}
.row-grid.gap-half-2 {
  gap: 15px;
}
.row-grid.gap-half-3 {
  gap: 25px;
}
.row-grid.gap-half-4 {
  gap: 35px;
}
.row-grid.gap-half-5 {
  gap: 45px;
}
.row-grid .col-grid {
  display: grid;
  grid-row: span 1;
  grid-template-rows: max-content max-content;
  grid-template-rows: subgrid;
}

footer {
  color: var(--dark-gray);
}
footer a.nav-link {
  color: var(--dark-gray);
}
footer a {
  color: inherit;
}